
body {
    font-family: Helvetica, sans-serif;
}
.iconClassRoot {
    width: 15px;
    height: 15px;
    display: inline-block;
    background: url("../images/ztree/root.png") no-repeat center/100% auto;
}
.iconClassNode {
    width: 15px;
    height: 15px;
    display: inline-block;
    background: url("../images/ztree/node.png") no-repeat center/100% auto;
}
.operate {
    display: flex;
}
.operate ul > li {
    float: left;
    margin: 10px 10px;
    list-style-type: none;
}



